<h1 id="单链表" tabindex="-1"><a class="header-anchor" href="#单链表" aria-hidden="true">#</a> 单链表</h1>
<h2 id="单链表的特点" tabindex="-1"><a class="header-anchor" href="#单链表的特点" aria-hidden="true">#</a> 单链表的特点</h2>
<p>通过指针来表达线性的数据之间的逻辑关系，通过指针把它的一串存储节点链接成一个链</p>
<p>存储节点有两部分组成：数据域 + 指针域</p>
<p>指针域：指向后继元素的存储地址</p>
<p>插入，删除不需要移动数据元素，只需要修改链</p>
<p>不要求逻辑上相邻的两个元素物理上也相邻；通过链建立起数据元素之间的逻辑关系</p>
<p>存储密度小于1</p>
